// store/index.js
import { createStore } from 'vuex';

const store = createStore({
  state() {
    return {
      houses: [],
      searchTerm: '',
      searchCategory: 'city' // Default search category
    };
  },
  getters: {
    filteredHouses(state) {
      const term = state.searchTerm.toLowerCase();
      const category = state.searchCategory;

      if (!term) return state.houses;

      return state.houses.filter(house => {
        let value = '';

        if (category === 'city') {
          value = house.location.city;
        } else if (category === 'description') {
          value = house.description;
        } else if (category === 'price') {
          value = house.price.toString();
        } else if (category === 'size') {
          value = house.size.toString();
        }

        return value.toLowerCase().includes(term);
      });
    }
  },
  mutations: {
    setHouses(state, houses) {
      state.houses = houses;
    },
    setSearchTerm(state, term) {
      state.searchTerm = term;
    },
    setSearchCategory(state, category) {
      state.searchCategory = category;
    }
  },
  actions: {
    async fetchHouses({ commit }) {
      try {
        const response = await fetch('https://api.intern.d-tt.nl/api/houses', {
          headers: {
            'X-API-KEY': 'U4tnCWEYc37uJdDpN8oyiQOGqXTajzgK'
          }
        });
        const houses = await response.json();
        commit('setHouses', houses);
      } catch (error) {
        console.error('Error fetching houses:', error);
      }
    }
  }
});

export default store;
